<!-- 首页 -->
<template>
	<section class='home'>
		<el-container :style="{height:window_height + 'px'}">

			<!-- 导航部分 -->

			<!-- 侧边导航 -->
			<el-container>
				<el-aside width="200px">

					<el-menu 
                        background-color="#30343f" 
                        text-color="#fff" 
                        active-text-color="#ffd04b" 
                        router 
                        :default-active="currentRoute" 
                        class="el-menu-vertical-demo"
                        @open="handleOpen" 
                        @close="handleClose"
                    >

						<div class="logo">
                            <img src="@/assets/images/a3bb004f664105c88d331258b4f9308.png" alt="">
						</div>

                        <el-submenu index="1">
							<template slot="title">
								<i class="el-icon-video-camera-solid"></i>
								<span>视频管理</span>
							</template>
							<el-menu-item-group>
								<el-menu-item index="video">视频列表</el-menu-item>
							</el-menu-item-group>
							<el-menu-item-group>
								<el-menu-item index="video_type">视频分类</el-menu-item>
							</el-menu-item-group>

						</el-submenu>

						<el-submenu index="2">
							<template slot="title">
								<i class="el-icon-user-solid"></i>
								<span>人员管理</span>
							</template>
							<el-menu-item-group>
								<el-menu-item index="admin">管理员</el-menu-item>
								<el-menu-item index="user">用户</el-menu-item>
							</el-menu-item-group>

						</el-submenu>

						

						<!-- <el-submenu index="3">
							<template slot="title">
								<i class="el-icon-location"></i>
								<span>{{getItem('user_realName')}}</span>
							</template>
							<el-menu-item-group>
								<el-menu-item index="3-1">选项1</el-menu-item>
								<el-menu-item index="3-2">选项2</el-menu-item>
							</el-menu-item-group>

						</el-submenu> -->

					</el-menu>
				</el-aside>

				<!-- 主体部分 -->
				<el-main :style="{height:window_height + 'px'}">
					<!-- <el-page-header @back="goBack" title="" :content="title"></el-page-header> -->
					<el-header class="header">

						<el-page-header @back="$router.go(-1)" :content="$route.name"></el-page-header>

						<div class="">
							<el-dropdown @command="handleCommand" trigger="click">
								<span class="el-dropdown-link flex flex_align_center">
									<el-link class="mr20" type="primary">{{getItem('user_realName')}}</el-link>
									<el-avatar size="medium" src="https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png"></el-avatar>
								</span>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item command="err">退出登录</el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>

						</div>

					</el-header>

					<section :style="{height:window_height - 80+ 'px'}" style="padding:10px 20px;overflow-y: auto;">
						<router-view></router-view>
					</section>

                    <!-- <p class="introduce">山西众驰通教育有限公司</p> -->
				</el-main>
			</el-container>
		</el-container>
	</section>
</template>
   
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
	data() {
		return {
			window_height: '',
			title: '',
			currentRoute: '',
		};
	},

	created() {
        this.window_height = this.getViewportSize().height ;
        
        console.log(this.$route);
	},

	mounted() {
        this.currentRoute = this.$route.path.split('/')[2];
        console.log(this.currentRoute);
	},

	methods: {
		handleCommand(command) {
			if (command == 'err') {
				this.removeItem();
				this.hf('/login');
			}

		},
		goBack() {
			console.log('go back');
			this.$router.go(-1)
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
		},
		handleClose(key, keyPath) {
			console.log(key, keyPath);
		},
		// ...mapMutations({ user })
	},
	updated() {

	},
	beforeRouteUpdate(to, from, next) {

		this.title = to.name
		next()
	},
};
</script>
   
<style lang='less' scoped>
.home {
	/deep/.el-main {
		padding: 0;
		padding-bottom: 0;

	}

	/deep/.el-menu-vertical-demo {
		height: 100%;
	}

	.logo {
        padding-left: 20px;
        padding-right: 20px;
        box-sizing: border-box;
		height: 100px;
		display: flex;
        align-items: center;
        font: 900 22px '';
        color: #fff;
        cursor: pointer;

        img {
            height: 80px;
        }
	}

	.header {
        // height: 50px !important;
        
		display: flex;
		justify-content: space-between;
		align-items: center;
		border: 0;
		border-bottom: 1px solid #f1f1f1;
    }
    
    .introduce {
        color: #797979;
        font-size: 12px;
        padding-left: 20px;
        // text-align: right;
    }

	ul {
		border: 0;
	}
}
