<!-- video_type -->
<template>
	<section class='video_type'>

		<header class="mt20">
			<el-button @click="addView" class="ml20" type="success">添加分类</el-button>
		</header>

		<el-table ref="table" :data="tableData" max-height="800px" stripe highlight-current-row style="width: 100%;margin-top:20px">
			<el-table-column type="index" :index="indexMethod"></el-table-column>
			<el-table-column prop="name" label="标题" width="300"></el-table-column>
			<el-table-column prop="establishTime" label="创建时间"></el-table-column>

			<el-table-column fixed="right" label="操作" width="180">
				<template slot-scope="scope">

					<el-button @click.native.prevent="editUser(scope.$index, tableData)" type="text" size="small">
						编辑
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						移除
					</el-button>

				</template>
			</el-table-column>

		</el-table>

		<el-dialog @close="ruleForm.name = ''" :title="title_index == 0?'添加视频类':'编辑视频类型'" destroy-on-close :visible.sync="dialogVisible" width="30%" top="10vh">
			<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

				<el-form-item label="类型名称" prop="name">
					<el-input v-model="ruleForm.name"></el-input>
				</el-form-item>

				<el-form-item class="mt20">
					<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
					<el-button @click="dialogVisible = false">取消</el-button>
				</el-form-item>

			</el-form>
		</el-dialog>
	</section>
</template>
   
<script>
export default {
	data() {
		return {
			tableData: [],
			dialogVisible: false,
			ruleForm: {
				name: ''
			},
			rules: {
				name: [
					{ required: true, message: '请输入视频类型', trigger: 'blur' },
					{ min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
				],
			},
			title_index: 0
		};
	},

	created() {

	},

	mounted() {
		this.getAllvideoType()
	},

	methods: {
		// 提交
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {

					if (this.title_index == 0) {
						// 新增
						this.post('videoType/addVideoType', {
							name: this.ruleForm.name
						}).then(res => {
							if (res) {
								this.$message({
									message: `恭喜你，添加 【${this.ruleForm.name}】 类型成功`,
									type: 'success'
								});

								this.ruleForm.name = '';
								this.dialogVisible = false;
								this.getAllvideoType()
							}
						})
					} else {
						// 编辑
						this.post('videoType/editVideoType', {
							name: this.ruleForm.name,
							id: this.title_index
						}).then(res => {
							if (res) {
								this.$message({
									message: `恭喜你，编辑 【${this.ruleForm.name}】 类型成功`,
									type: 'success'
								});

								this.ruleForm.name = '';
								this.dialogVisible = false;
								this.getAllvideoType()
							}
						})


					}
				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		indexMethod(index) {
			return index + 1;
		},
		// 获取列表
		async getAllvideoType() {
			let res = await this.get('videoType/getAllvideoType');
			console.log(res);
			if (res) {
				let data = res.data;
				this.tableData = data;
			}
		},
		// 编辑
		editUser(index, rows) {
			console.log(rows[index]);
			this.title_index = rows[index].id;
			this.dialogVisible = true;
			this.ruleForm.name = rows[index].name
		},

		// 删除
		deleteRow(index, rows) {
			console.log(rows[index]);
			this.$confirm('此操作将永久删除, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.delete('videoType/deleteVideoType/' + rows[index].id).then(res => {
					this.$message({
						message: `恭喜你，删除 【${rows[index].name}】成功`,
						type: 'success'
					});

					this.getAllvideoType()
				})
			})


		},

		// 添加分类
		addView() {
			this.title_index = 0;
			this.dialogVisible = true;
		}
	},
	updated() {

	}
};
</script>
   
<style lang='less' scoped>
.video_type {
}
</style>