//组件模块

import Home from "@/views/home/home";
import User from "@/views/home/user/user";
import AddUser from "@/views/home/user/addUser.vue";
import Admin from "@/views/home/admin/admin";
import Video from "@/views/home/video/video";
import Addview from "@/views/home/video/addview";
import Video_type from "@/views/home/video_type/video_type";

const home = [{
    path: "/home",
    name: "首页",
    component: Home,
    redirect: "/home/video",
    meta: {
        keepAlive: true,
        requiresAuth: true
    },
    children: [{
        path: 'user',
        name: '用户管理',
        component: User,
        meta: {
            requiresAuth: false
        }
    }, {
        path: 'adduser',
        name: '操作用户',
        component: AddUser
    }, {
        path: 'admin',
        name: '管理员',
        component: Admin,
        meta: {
            requiresAuth: false
        },
    }, {
        path: 'video',
        name: '视频管理',
        component: Video,
        meta: {
            requiresAuth: false
        }
    }, {
        path: 'addview',
        name: '操作视频',
        component: Addview,
        meta: {
            requiresAuth: false
        }
    }, {
        path: 'video_type',
        name: '视频分类',
        component: Video_type,
        meta: {
            requiresAuth: false
        }
    }],
}];

export default home;