<template>
	<!-- 视频管理 -->
	<div class="z-video">

		<!-- header -->
		<header class="mt20">

			<span class="demonstration">发布时间</span>
			<el-date-picker class="ml20 mr20" value-format="yyyy-MM-dd HH:mm:ss" v-model="value1" type="datetimerange" :picker-options="pickerOptions" range-separator="至"
				start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>

			<span class="demonstration mr20">类型</span>
			<el-select v-model="file_class" filterable placeholder="请选择">
				<el-option label="全部" :value="''"></el-option>
				<el-option v-for="item in video_type" :key="item.id" :label="item.name" :value="item.id">
				</el-option>
			</el-select>

			<span class="demonstration mr20 ml20">状态</span>
			<el-select v-model="state" placeholder="请选择">
				<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
				</el-option>
			</el-select>

			<el-button @click="onQuery" type="primary" class="ml20">查询</el-button>
			<el-button @click="addView" class="ml20" type="success">添加视频</el-button>

			<el-tag class="ml20">{{total}}</el-tag>
		</header>

		<el-table ref="table" :data="tableData" stripe highlight-current-row style="width: 100%;margin-top:20px">
			<el-table-column type="index" :index="indexMethod"></el-table-column>
			<el-table-column prop="title" label="封面" width="120">
				<template slot-scope="scope" class="s">
					<div class="cover">
						<i @click="playView(scope.$index, tableData)" class="el-icon-video-play"></i>
						<img :src="scope.row.coverBase" />
					</div>
				</template>
			</el-table-column>
			<el-table-column prop="title" label="标题" width="230"></el-table-column>
			<el-table-column prop="introduction" label="简介"></el-table-column>
			<el-table-column prop="releaseTime" label="发布时间" width="180" sortable></el-table-column>
			<el-table-column prop="volume" label="播放量(次)" width="110" align="center" sortable></el-table-column>
			<el-table-column prop="type" label="类型" width="120">
				<template slot-scope="scope">
					<span>{{scope.row.type | VideoType}}</span>
				</template>
			</el-table-column>
			<el-table-column prop="shelf" label="状态" width="80">
				<template slot-scope="scope">
					<span v-if="scope.row.shelf == '1' " style="color:#67C23A">启用</span>
					<span v-else style="color:red">禁用</span>
				</template>
			</el-table-column>

			<el-table-column fixed="right" label="操作" width="180">
				<template slot-scope="scope">

					<el-button v-if="scope.row.shelf == '0'" @click.native.prevent="Enable(scope.$index, tableData, 1)" type="text" size="small">
						启用
					</el-button>

					<el-button v-if="scope.row.shelf == '1'" @click.native.prevent="Enable(scope.$index, tableData, 0)" type="text" size="small">
						禁用
					</el-button>

					<el-button @click.native.prevent="editUser(scope.$index, tableData)" type="text" size="small">
						编辑
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						移除
					</el-button>

				</template>
			</el-table-column>

		</el-table>

		<div class="block mt20 tr">
			<el-pagination @current-change="changeCurr" layout="prev, pager, next" :total="total"></el-pagination>
		</div>

		<el-dialog title="视频审核" destroy-on-close :visible.sync="dialogVisible" width="60%" top="10vh">
			<video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"></video-player>
		</el-dialog>

	</div>
</template>

<script>
export default {
	data() {
		const that = this;
		return {
			pickerOptions: {
				shortcuts: [{
					text: '最近一周',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近一个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近三个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
						picker.$emit('pick', [start, end]);
					}
				}]
			},
			playerOptions: {
				playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
				autoplay: false, //如果true,浏览器准备好时开始回放。
				muted: false, // 默认情况下将会消除任何音频。
				loop: false, // 导致视频一结束就重新开始。
				preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
				language: 'zh-CN',
				aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
				fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
				sources: [{
					type: "",//这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
					src: "" //url地址
				}],
				poster: "", //你的封面地址
				// width: document.documentElement.clientWidth, //播放器宽度
				notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
				controlBar: {
					timeDivider: true,
					durationDisplay: true,
					remainingTimeDisplay: false,
					fullscreenToggle: true  //全屏按钮
				}
			},
			dialogVisible: false,
			dialogVisible1: false,
			options: [{
				value: "",
				label: '全部',
			}, {
				value: "1",
				label: '启用',
			}, {
				value: "0",
				label: '禁用',
			}],
			state: '',
			value1: [],
			tableData: [],
			total: 0,
			video_type: [],
			file_class: ''
		}
	},
	mounted() {
		window.that = this
	},
	created() {
		this.getViewList();
		this.getAllvideoType()
	},
	filters: {
		VideoType(type) {


			let typeNmae = ''

			for (let i in that.video_type) {
				if (type == that.video_type[i].id) {
					typeNmae = that.video_type[i].name;
				}
			}

			return typeNmae == '' ? '暂无' : typeNmae
		}
	},
	methods: {

		/// 启用&&停用
		Enable(index, rows, states) {
			console.log(rows[index]);
			console.log(states);
			this.post('/view/isenable', {
				id: rows[index].id,
				states: states
			}).then(res => {
				console.log(res);
				this.tableData[index].shelf = states;
			})
		},

		async getAllvideoType() {
			let res = await this.get('videoType/getAllvideoType');
			console.log(res);
			if (res) {
				let data = res.data;
				this.video_type = data;
			}
		},
		// 查询
		onQuery() {
			this.getViewList();
		},
		// 添加视频
		addView() {
			this.hf('/home/addview')
		},
		// 编辑
		editUser(index, rows) {
            console.log(rows[index]);
            this.hf('/home/addview', rows[index].id);
		},
		// 移除
		deleteRow(index, rows) {
			console.log(rows[index]);
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {

				this.delete('/view/deleteVideo/' + rows[index].id,{
                    file_type:rows[index].file_type,
                    viewFileUrl:rows[index].viewFileUrl.split('/')[2]
                }).then(res => {
					if (res.code == 200) {
						this.$message({
							type: 'success',
							message: '删除成功!'
						});
						this.getViewList()
					}
				})

			})
		},
		// 分页
		changeCurr(e) {
			console.log(e);
			this.page = e;
			this.getViewList()
		},
		// 播放视频
		playView(index, rows) {
			console.log(rows[index]);
			let data = rows[index];

			if (data.file_type == 1) {
				this.playerOptions.sources[0].src = data.viewFileUrl;
				this.playerOptions.poster = data.coverBase

			} else {
				this.playerOptions.sources[0].src = this.http + data.viewFileUrl;
				this.playerOptions.poster = data.coverBase

			}

			console.log(this.playerOptions.sources[0].src);

			this.dialogVisible = true;
		},
		indexMethod(index) {
			return index + 1;
		},
		// 获取列表
		async getViewList() {

			let startTiem, endTiem = '';
			if (this.value1 != null) {
				startTiem = this.value1[0];
				endTiem = this.value1[1];
			} else {
				startTiem, endTiem = ''
			}

			let res = await this.get('/view/getViewList', {
				page: this.page,
				count: 10,
				state: this.state,
				startTiem,
				endTiem,
				type: this.file_class
			})

			if (res) {
				console.log(res);
				let data = res.data;

				this.tableData = data;
				this.total = res.total
			}
		}
	}
}
</script>

<style lang="less" scoped>
.z-video {
	.cover {
		height: 50px;
		position: relative;

		i {
			position: absolute;
			font-size: 20px;
			color: #fff;
			top: 18px;
			z-index: 9;
			left: 32%;
			cursor: pointer;
			transition: all 0.1s ease-out;

			&:hover {
				transform: scale(1.1);
			}
		}

		img {
			height: 100%;
		}
	}
}
</style>