// 引入需要用到的资源,qs和element可以百度搜索怎么下载

import axios from "axios";
import qs from "qs";
import app from '../main'
import { Message, Loading, Col } from 'element-ui'; // 主要用于提示，可以换成自己用的ui框架
let loadingInstance = null

const server = {
    pro: "https://doctor.mipmci.com/zuul/api/", //生产环境
    dev: "http://39.100.122.13:3000/api/", //测试环境
    util: "http://127.0.0.1:3000/api/", //其他环境
    // util: "http://39.100.122.13:3000/" //其他环境
};

const origin = location.hostname;

let DOMIN = "";

for (let i in server) {
    let item = server[i];
    if (item.indexOf(origin) > -1) {
        DOMIN = item;
    }
}

if (!DOMIN) DOMIN = server.util ? server.util : server.dev;

/*
  axios实例 
 */
const service = axios.create({
    baseURL: DOMIN,
    timeout: 5000 // 请求超时时间
});

/*
  request拦截器 
 */
service.interceptors.request.use(config => {

    // loadingInstance = Loading.service({
    //     lock: true,
    //     text: 'loading...',
    //     background: 'rgba(255, 255, 255)'
    // })


    let header = "application/x-www-form-urlencoded";
    let objData = JSON.stringify(config.headers['Content-Type']) == JSON.stringify(header) ?
        qs.stringify({...config.data }) : {...config.data };

    config.method === 'post' ?
        config.data = objData :
        config.params = {...config.params };

    if (app.getItem("token")) config.headers["authorization"] = 'Bearer ' + app.getItem("token");

    return config;

}, error => { //请求错误处理

    // loadingInstance.close()


    Message('请求错误')
    Promise.reject(error)
});

/* 
 respone拦截器 
*/
service.interceptors.response.use(
    response => {
        //这里根据后端提供的数据进行对应的处理
        let code = response.data.code;
        // loadingInstance.close()

        console.log(code);
        if (code == "200") {
            return response.data;
        } else {
            app.$message.error(response.data.msg);

            return false;
        }
    },
    error => {
        //响应错误处理

        // loadingInstance.close()

        console.log(error);

        console.log("error");


        app.hf('/login')
        Message("管理员信息已过期，请重新登录");

        return;
        // return Promise.reject(error)
    }
);

export default {
    service,
    DOMIN
};