import _vue from "@/main.js";
export default {
    install(Vue, options) {

        // 存储本地缓存
        Vue.prototype.setItem = function (key, value) {
            window.localStorage.setItem(key, JSON.stringify(value));
        };
  
        // 获取本地缓存
        Vue.prototype.getItem = function (key) {
            return JSON.parse(window.localStorage.getItem(key) || "[]");
        };

        // 清除本地缓存
        Vue.prototype.removeItem = function () {
            window.localStorage.clear();
        };
        // 清除本地指定缓存
        Vue.prototype.TheremoveItem = function (key) {
            Object.keys(localStorage).forEach(item => {
                item != "themeColor" ? localStorage.removeItem(item) : "";
            });
        };

        Vue.prototype.getViewportSize = function () {
            return {
                width: window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth,
                height: window.innerHeight || document.documentElement.clientHeight ||  document.body.clientHeight
            };
        };
        Vue.prototype.hf = function (path, params) {
            console.log(path);
            _vue.$router.push({
                path: path,
                query: {
                    id: JSON.stringify(params)
                }
                // query:id
            });
        };

        // 获取时间
        Vue.prototype.getDate = function (dates = new Date()) {
            var now = dates;
            var year = now.getFullYear(); //得到年份
            var month = now.getMonth(); //得到月份
            var date = now.getDate(); //得到日期
            var day = now.getDay(); //得到周几
            var hour = now.getHours(); //得到小时
            var minu = now.getMinutes(); //得到分钟
            var sec = now.getSeconds(); //得到秒
            var MS = now.getMilliseconds(); //获取毫秒
            var week;
            month = month + 1;
            if (month < 10) month = "0" + month;
            if (date < 10) date = "0" + date;
            if (hour < 10) hour = "0" + hour;
            if (minu < 10) minu = "0" + minu;
            if (sec < 10) sec = "0" + sec;
            if (MS < 100) MS = "0" + MS;

            var arr_week = new Array(
                "星期日",
                "星期一",
                "星期二",
                "星期三",
                "星期四",
                "星期五",
                "星期六"
            );
            week = arr_week[day];

            var time = "";

            var items = "";
            // + ":" + sec ;秒
            time = year + "." + month + "." + date + " " + hour + ":" + minu;

            items =
                year +
                "-" +
                month +
                "-" +
                date +
                " " +
                hour +
                ":" +
                minu +
                ":" +
                sec;

            return {
                week: week,
                time: time,
                items: items
            };
        };
        
    }
};