<template>
	<!-- 用户管理 -->
	<div class="z-user">
		<!-- 页面 -->

		<!-- header -->
		<header>

			<el-cascader clearable class="mr20" placeholder='区域查询' size="large" :options="options" v-model="selectedOptions" @change="handleChange"></el-cascader>
			<el-input style="width:222px" v-model="phone" placeholder="手机号查询"></el-input>
			<el-button @click="onQuery" class="ml20" type="primary">查询</el-button>
			<el-button @click="addUser" class="ml20" type="success">添加用户</el-button>

			<el-tag class="ml20">{{total}}</el-tag>
		</header>

        <!-- 列表 -->
		<el-table ref="table" max-height="800px" :data="tableData" stripe highlight-current-row style="width: 100%">
			<el-table-column type="index" :index="indexMethod"></el-table-column>
			<el-table-column prop="areaName" label="所在区域" width="230"></el-table-column>
			<el-table-column prop="name" label="用户名" width="180"></el-table-column>
			<el-table-column prop="phone" label="手机号" width="180"></el-table-column>
			<el-table-column prop="introduce" label="简介"></el-table-column>
			<el-table-column prop="registeredDate" sortable label="注册时间"></el-table-column>
			<el-table-column fixed="right" label="操作" width="180">
				<template slot-scope="scope">

					<el-button @click.native.prevent="editUser(scope.$index, tableData)" type="text" size="small">
						编辑
					</el-button>

					<el-button @click.native.prevent="initialization(scope.$index, tableData)" type="text" size="small">
						密码重置
					</el-button>

					<el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
						移除
					</el-button>

				</template>
			</el-table-column>

		</el-table>

		<div class="block mt20 tr">
			<el-pagination @current-change="changeCurr" layout="prev, pager, next" :total="total">
			</el-pagination>
		</div>

	</div>
</template>

<script>
import { regionDataPlus } from 'element-china-area-data'
export default {
	data() {
		return {
			table_height: '',
			currentPage3: 0,
			tableData: [],
			phone: '',
			total: 0,
			dialogFormVisible: false,
			count: 10,
			loading: false,
            noMore: false,
            area:'',

			options: regionDataPlus ,
			selectedOptions: []
		}
	},
	created() {
		this.table_height = this.getViewportSize().height - 155 + 'px';
		this.getUserList(1)
	},
	mounted() {

		console.log(this.$refs.table.$el);

	},
	methods: {

		handleChange(value) {
            console.log(value)
            this.area = JSON.stringify(value)
            
            // this.getUserList()
		},
		indexMethod(index) {
			return index + 1;
		},

		changeCurr(e) {
			console.log(e);
			this.getUserList(e)
		},

		// 跳转添加用户页面
		addUser() {
			// this.dialogFormVisible = true;
			this.hf('/home/adduser')
		},

		// 手机号查询
		onQuery() {
			this.getUserList()
		},

		// 密码重置
		initialization(index, rows) {


			this.$confirm('重置密码后，默认密码为 123456 确定要初始化？', '重置密码', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.get('/users/initialization/' + rows[index].id).then(res => {
					if (res.code == 200) {
						this.$message({
							message: `恭喜【 ${rows[index].name} 】密码重置成功`,
							type: 'success'
						});
					}
				})
			}).catch(() => {
				console.log('已取消删除');
			});
		},
		// 编辑成员
		editUser(index, rows) {
			console.log(rows[index]);
			this.hf('/home/adduser', rows[index].id);
		},
		// 删除成员
		deleteRow(index, rows) {
			console.log(rows[index]);
			this.$confirm('将永久删除此用户，确定要删除？', '删除用户', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.delete(`/users/userdelete/${rows[index].id}`).then(res => {
					console.log(res);
					if (res.code == 200) {
						this.getUserList(1)
						this.$message({
							message: '删除成功',
							type: 'success'
						});
					}
				})
			}).catch(() => {
				console.log('已取消删除');
			});

		},
		async getUserList(page, count) {
			let res = await this.get('/users', {
				page,
				count: this.count,
                phone: this.phone,
                area: this.area
			});
			console.log(res);
			if (res) {
				let data = res.data;

				this.tableData = data.list;
				this.total = data.total;
			}
		},

		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.getUserList(val)
		},

		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.form['birthday'] = this.rTime(this.form.registeredDate)
					this.post('/users/adduser', this.form).then(res => {
						console.log(res);
						if (res.code == 200) {
							this.$message({
								message: '添加成功',
								type: 'success'
							});

							this.dialogFormVisible = false;
						}
					})
				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		rTime(date) {
			var json_date = new Date(date).toJSON();
			return new Date(new Date(json_date) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
		}
	}
}
</script>

<style lang="less" scoped>
.z-user {
	header {
		height: 60px;
		display: flex;
		align-items: center;
	}
	/deep/.el-date-editor {
		width: 100%;
	}
}
</style>