<template>
	<div class="addUser">

		<el-form ref="ruleForm" class="pt20 pl30 pr30 demo-ruleForm" :rules="rules" :model="form" label-position="top" label-width="120px">
			<el-form-item label="用户头像">
				<el-upload class="avatar-uploader" action="#" :http-request="httpRequest1" :show-file-list="false" :on-success="handleAvatarSuccess"
					:before-upload="beforeAvatarUpload">

					<img v-if="form.head_img" :src="form.head_img" class="avatar">

					<i v-else class="el-icon-plus avatar-uploader-icon"></i>

				</el-upload>

			</el-form-item>

			<el-divider></el-divider>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="用户名称" prop="name">
						<el-input v-model="form.name" placeholder="请输入"></el-input>
					</el-form-item>

				</el-col>

				<el-col :span="12">
					<el-form-item label="手机号" prop="phone">
						<el-input v-model="form.phone" maxlength="11" placeholder="请输入"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="性别">
						<el-radio-group v-model="form.sex">
							<el-radio label="男"></el-radio>
							<el-radio label="女"></el-radio>
						</el-radio-group>
					</el-form-item>

				</el-col>

				<el-col :span="12">
					<el-form-item label="生日：">
						<el-date-picker v-model="form.birthday" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
						</el-date-picker>
					</el-form-item>
				</el-col>
			</el-row>

			<el-divider></el-divider>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="所在地区：" prop="area">
						<el-cascader size="large" :options="options" v-model="form.area" @change="handleChange"></el-cascader>
					</el-form-item>

				</el-col>

				<el-col :span="12">
					<el-form-item label="真实姓名：">
						<el-input v-model="form.realName" placeholder="请输入"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="职位：">
						<el-input v-model="form.position" placeholder="请输入"></el-input>
					</el-form-item>

				</el-col>

				<el-col :span="12">
					<el-form-item label="行业：">
						<el-input v-model="form.industry" placeholder="请输入"></el-input>
					</el-form-item>
				</el-col>
			</el-row>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="学历">
						<el-input v-model="form.record" placeholder="请输入 "></el-input>
					</el-form-item>

				</el-col>

				<el-col :span="12" prop="email">
					<el-form-item label="邮箱">
						<el-input v-model="form.email" placeholder="请输入"></el-input>
					</el-form-item>

				</el-col>
			</el-row>

			<el-form-item label="简介：" class="introduce">
                <el-input  type="textarea" v-model="form.introduce" placeholder="请输入" ></el-input>
			</el-form-item>

			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
				<el-button @click="hf('/home/user')">取消</el-button>
			</el-form-item>

		</el-form>
	</div>
</template>

<script>
import { regionData,TextToCode,CodeToText } from 'element-china-area-data'
export default {
	data() {
		var checkAge = (rule, value, callback) => {
			if (!value) {
				return callback(new Error('手机号不能为空'));
			}
			if (!(/^1[3456789]\d{9}$/.test(value))) {
				return callback(new Error('手机号码有误，请重填'));
			} else {
				callback();
			}
		};
		return {
			options: regionData,
			imageUrl: '',
			table_height: '',
			form: {
				name: '',
                phone: '',
                area:'',
				sex: '1',
				email: '',
				head_img: '',
				birthday: '',
				introduce: '',
				introduce: '',
				realName: '',
				position: '',
				industry: '',
				company: '',
				record: '',

			},
			rules: {
				name: [
					{ required: true, message: '请输入名称', trigger: 'blur' },
					{ min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
				],
				phone: [
					// {validator: checkAge, required: true, message: '请输入正确的手机号', trigger: 'blur' }
					{ required: true, validator: checkAge, trigger: 'blur' }
				],
				head_img: [
					{ required: true, message: '请选择用户头像', trigger: 'change' }
				],
				email: [
					{ type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                ],
                area: [
					{ required: true, message: '请选择用户所在区域', trigger: ['blur', 'change'] }
				],
			}
		}
	},
	created() {
		console.log(this.$route.query.id != undefined);

		if (this.$route.query.id != undefined) {
			this.get('/users/getUserInformation/' + this.$route.query.id).then(res => {
				console.log(res);
				let data = res.data;

				for (let i in data) {
					this.form[i] = data[i];
				}
                this.form['sex'] = this.form.sex == '1' ? '男' : '女'

                if(this.form.area!=null){
                    this.form['area'] = JSON.parse(this.form.area)
                }
			})
		}
	},
	methods: {
		handleChange(value) {
            console.log(value)
            console.log(CodeToText[value[0]]);
            console.log(CodeToText[value[1]]);
            console.log(CodeToText[value[2]]);
		},
		// 提交表单
		submitForm(formName) {
            console.log('submit!');

			this.$refs[formName].validate((valid) => {
				if (valid) {
                    this.form['sex'] = this.form.sex == '男' ? '1' : '0'
                    this.form['area'] = JSON.stringify(this.form.area);


                    console.log(this.form['area'] );

					// 编辑
					if (this.$route.query.id != undefined) {

						this.form['id'] = this.$route.query.id
						this.post('/users/edituser', this.form, 2).then(res => {
							console.log(res);

							if (res.code == 200) {
								this.$message({
									message: `恭喜你，编辑【 ${this.form.name} 】成功`,
									type: 'success'
								});

								this.hf('/home/user')
							} else {
                                this.$message.error(res.msg);
                                this.form['area'] = JSON.parse(this.form.area)
							}
						})
					} else {
						// 新增
						this.post('/users/adduser', this.form).then(res => {

							console.log('新增');
							console.log(res);

							if (res.code == 200) {
								this.$message({
									message: `恭喜你，添加【 ${this.form.name} 】成功`,
									type: 'success'
								});

								this.hf('/home/user')
							} else {
                                this.$message.error(res.msg);
                                this.form['area'] = JSON.parse(this.form.area)
							}
						})
					}

				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		handleAvatarSuccess(res, file) {
			this.imageUrl = URL.createObjectURL(file.raw);
		},
		beforeAvatarUpload(file) {
			const isJPG = file.type === 'image/jpeg';
			const isLt2M = file.size / 1024 / 1024 < 2;

			if (!isJPG) {
				this.$message.error('上传头像图片只能是 JPG 格式!');
			}
			if (!isLt2M) {
				this.$message.error('上传头像图片大小不能超过 2MB!');
			}
			return isJPG && isLt2M;
		},
		// 上传bs64 (1)
		httpRequest1(data, index) {
			console.log(index);
			let _this = this
			let rd = new FileReader() // 创建文件读取对象
			let file = data.file
			rd.readAsDataURL(file) // 文件读取装换为base64类型
			rd.onloadend = function (e) {
				_this.form.head_img = this.result
			}
		},
	},

}
</script>

<style lang="less" scoped>
.addUser {
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	.avatar-uploader .el-upload:hover {
		border-color: #409eff;
	}
	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 120px;
		height: 120px;
		line-height: 120px;
		text-align: center;
	}
	.avatar {
		width: 120px;
		height: 120px;
		display: block;
	}
	/deep/.el-upload {
		border: 1px solid #eee;
	}

	/deep/.el-form-item__content,
    .el-date-editor,
    .el-cascader {
		width: 500px;
    }
    
    /deep/.introduce .el-form-item__content{
        width: 100%!important;
    }
}
</style>