<template>
	<div class="addView">

		<el-form ref="ruleForm" class="pt20 pl30 pr30 demo-ruleForm" :rules="rules" :model="form" label-position="top" label-width="120px">

			<el-form-item label="视频封面">
				<el-upload class="avatar-uploader" :before-upload="beforeAvatarUpload" action="#" :http-request="httpRequest1" :show-file-list="false" :on-success="handleAvatarSuccess">

					<img v-if="form.coverBase" :src="form.coverBase" class="avatar">

					<i v-else class="el-icon-plus avatar-uploader-icon"></i>

				</el-upload>

			</el-form-item>

			<el-divider></el-divider>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="上传视频">
						<div>
							<el-radio v-model="form.file_type" label="1">在线地址</el-radio>
							<el-radio v-model="form.file_type" label="2">本地视频上传</el-radio>
						</div>

						<div class="mt20">
							<el-input v-if="form.file_type == 1" v-model="form.viewFileUrl" placeholder="请输入视频地址"></el-input>


                            <!-- :auto-upload="false" -->
							<el-upload  v-if="form.file_type == 2" ref="upload" class="upload-demo" drag name="file" :action="http + 'uploadFile'" :limit="1" :headers="{
                                    authorization:'Bearer ' + getItem('token')
                                    }" :data="{
                                        type:'video'
                                    }" 
                                    :before-upload="beforeUpload" 
                                    
                                    :on-exceed="handleExceed" 
                                    :on-success="onSuccess">
                                    
								<i class="el-icon-upload"></i>
								<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
								<div class="el-upload__tip" slot="tip">只能上传 mp4 文件</div>
							</el-upload>
						</div>

					</el-form-item>

				</el-col>

			</el-row>

			<el-divider></el-divider>

			<el-row :gutter="20">
				<el-col :span="12">
					<el-form-item label="标题">
						<el-input v-model="form.title" placeholder="请输入"></el-input>
					</el-form-item>

				</el-col>

				<el-col :span="12">
					<el-form-item label="视频类型">
						<el-select v-model="form.type" filterable placeholder="请选择">
							<el-option v-for="item in video_type" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>

			<el-form-item label="简介">
				<el-input type="textarea" v-model="form.introduction" placeholder="请输入"></el-input>
			</el-form-item>

			<el-form-item class="mt20">
				<el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
				<el-button @click="hf('/home/video')">取消</el-button>
			</el-form-item>

		</el-form>
	</div>
</template>

<script>
export default {
	data() {

		return {
			imageUrl: '',
			table_height: '',
			form: {
				title: '',
				coverBase: '',
				file_type: '1',
				viewFileUrl: '',
				introduction: '',
				type: ''
			},
			video_type: '',

			rules: {
				name: [
					{ required: true, message: '请输入名称', trigger: 'blur' },
					{ min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
				],
				phone: [
					// {validator: checkAge, required: true, message: '请输入正确的手机号', trigger: 'blur' }
				],
				email: [
					{ type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
				],
				area: [
					{ required: true, message: '请选择用户所在区域', trigger: ['blur', 'change'] }
				],
			}
		}
	},
	created() {
		// console.log(this.$route.query.id != undefined);

		if (this.$route.query.id != undefined) {
			this.get('/view/getVideoInformation/' + this.$route.query.id).then(res => {
				console.log(res);
				if (res) {
					let data = res.data;
					console.log(data);
					for (let i in data) {

                        this.form[i] = data[i];

						// if (i != 'viewFileUrl') {
							
						// } else {
						// 	if (data[i].file_type == 1) {

						// 	}
						// }
                    }
                    
						this.form.file_type = String(this.form.file_type);


				}
			})
		}
		this.getAllvideoType()
	},
	methods: {
		async getAllvideoType() {
			let res = await this.get('videoType/getAllvideoType');
			console.log(res);
			if (res) {
				let data = res.data;
				this.video_type = data;
			}
		},
		handleChange(value) {
			console.log(value)
			console.log(CodeToText[value[0]]);
			console.log(CodeToText[value[1]]);
			console.log(CodeToText[value[2]]);
		},
		// 提交表单
		submitForm(formName) {
			console.log('submit!');

			this.$refs[formName].validate((valid) => {
				if (valid) {

					// 编辑
					if (this.$route.query.id != undefined) {

						this.form['id'] = this.$route.query.id;
						this.post('view/editVideoInformation', this.form, 2).then(res => {

                            console.log(res);
							if (res.code == 200) {
								let data = res.data;
                                console.log(data);
                                
                                this.$message({
									message: `恭喜你，编辑【 ${this.form.title} 】成功`,
									type: 'success'
								});

								this.hf('/home/video')
							}
						})
					} else {
                        // if(this.form.file_type == 2) {
                        //     this.$refs.upload.submit();
                        // }
						this.post('/view/addView', this.form).then(res => {
							console.log(res);
							if (res) {
								this.$message({
									message: `恭喜你，发布【 ${this.form.title} 】成功`,
									type: 'success'
								});

								this.hf('/home/video')
							}
						})
					}



				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		handleAvatarSuccess(res, file) {
			this.imageUrl = URL.createObjectURL(file.raw);
		},
		beforeAvatarUpload(file) {
			const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
            
            console.log(isLt2M);

			if (!isJPG) {
				this.$message.error('上传头像图片只能是 JPG 格式!');
			}
			if (!isLt2M) {
				this.$message.error('上传头像图片大小不能超过 2MB!');
			}
			return isJPG && isLt2M;
		},
		// 上传bs64 (1)
		httpRequest1(data) {
			let _this = this
			let rd = new FileReader() // 创建文件读取对象
			let file = data.file
			rd.readAsDataURL(file) // 文件读取装换为base64类型
			rd.onloadend = function (e) {
                
				_this.compressImg(e.currentTarget.result, 500, 0.7).then(res => {
					_this.form.coverBase = res;
				})

			}
		},
		onSuccess(res) {
			console.log(res);
			if (res.code == 200) {
				this.form.viewFileUrl = res.data.url;
			}

		},

		beforeUpload(file) {
			console.log(file)
			var testmsg = file.name.substring(file.name.lastIndexOf('.') + 1)
			const extension = testmsg === 'mp4'
			// const isLt2M = file.size / 1024 / 1024 < 10
			if (!extension) {
				this.$message({
					message: '上传文件只能是 mp4 格式!',
					type: 'warning'
				});
			}
			// if(!isLt2M) {
			//     this.$message({
			//         message: '上传文件大小不能超过 10MB!',
			//         type: 'warning'
			//     });
			// }
			// return (extension || extension2) && isLt2M
			return extension
		},
		handleExceed(files, fileList) {
			this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
		},
		compressImg(base64String, w, quality) {
			var getMimeType = function (urlData) {
				var arr = urlData.split(",");
				var mime = arr[0].match(/:(.*?);/)[1];
				return mime;
			};
			var newImage = new Image();
			var imgWidth, imgHeight;

			var promise = new Promise(resolve => (newImage.onload = resolve));
			newImage.src = base64String;
			return promise.then(() => {
				imgWidth = newImage.width;
				imgHeight = newImage.height;
				var canvas = document.createElement("canvas");
				var ctx = canvas.getContext("2d");
				if (Math.max(imgWidth, imgHeight) > w) {
					if (imgWidth > imgHeight) {
						canvas.width = w;
						canvas.height = (w * imgHeight) / imgWidth;
					} else {
						canvas.height = w;
						canvas.width = (w * imgWidth) / imgHeight;
					}
				} else {
					canvas.width = imgWidth;
					canvas.height = imgHeight;
				}
				ctx.clearRect(0, 0, canvas.width, canvas.height);
				ctx.drawImage(newImage, 0, 0, canvas.width, canvas.height);
				var base64 = canvas.toDataURL(
					getMimeType(base64String),
					quality
				);
				return base64;
			});
		}
	},

}
</script>

<style lang="less" scoped>
.addView {
	.avatar-uploader .el-upload {
		border: 1px dashed #d9d9d9;
		border-radius: 6px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	.avatar-uploader .el-upload:hover {
		border-color: #409eff;
	}
	.avatar-uploader-icon {
		font-size: 28px;
		color: #8c939d;
		width: 300px;
		height: 120px;
		line-height: 120px;
		text-align: center;
	}
	.avatar {
		width: 300px;
		display: block;
	}
	/deep/.el-upload {
		border: 1px solid #eee;
	}

	/deep/.el-form-item__content,
	.el-date-editor,
	.el-cascader {
		width: 500px;
	}

	/deep/.introduce .el-form-item__content,
	.el-select {
		width: 100% !important;
	}
}
</style>