import Vue from "vue";
import VueRouter from "vue-router";

//组件模块
import Login from "@/components/login/login";

import Home from "./home";

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		redirect: "/login",
	},
	{
		path: "/login",
		name: "登录",
		component: Login,
	},

	...Home,
];

const router = new VueRouter({
	mode: "history",
	routes,
});

export default router;
