import service from "./apiconfig";

export default {
    install(Vue) {

        Vue.prototype.http = service.DOMIN

        Vue.prototype.post = function(url, data, rader = 1) {
            let comsont = new Object();
            switch (rader) {
                case 1:
                    comsont = {
                        "Content-Type": "application/x-www-form-urlencoded"
                    };
                    break;
                case 2:
                    comsont = {
                        "Content-Type": "application/json;charset=UTF-8"
                    };
                    break;
                case 3:
                    comsont = { "Content-Type": "application/json" };
                    break;
                default:
                    break;
            }
            return service.service({
                url: url,
                method: "post",
                headers: comsont,
                data
            });
        };
        Vue.prototype.get = function(url, data) {
            return service.service({
                url: url,
                method: "get",
                params: data
            });
        };
        Vue.prototype.delete = function(url, data) {
            return service.service({
                url: url,
                method: "delete",
                params: data
            });
        };

        Vue.prototype.put = function(url, data, rader = 1) {
            let comsont = new Object();
            switch (rader) {
                case 1:
                    comsont = {
                        "Content-Type": "application/x-www-form-urlencoded"
                    };
                    break;
                case 2:
                    comsont = {
                        "Content-Type": "application/json;charset=UTF-8"
                    };
                    break;
                case 3:
                    comsont = { "Content-Type": "application/json" };
                    break;
                default:
                    break;
            }
            return service.service({
                url: url,
                method: "put",
                headers: comsont,
                data
            });
        };
    }
};